.nav {
  margin: 0;
    display: flex;
    align-items: center;
    position: fixed;
    justify-content: space-around;
    bottom: 0;
    left: 0;
    right: 0;
    height: 70px;
    background-color: white;
    box-shadow: 0px 5px 13px rgb(0 0 0 / 14%);
    padding: 0 0 6px;
}
@media screen and (min-width: 992px) {
  .nav {
    position: absolute;
    right: 12px;
    bottom: unset;
    left: unset;
    width: 550px;
    border-radius: 100px;
    height: 50px;
    padding: 0;
    background: none;
    box-shadow: none;
  }
}

li {
  display: inline;
  align-items: center;
  list-style-type: disc;
  list-style: none;
  padding-top: 5px;
}
 
.head {  
  padding: 12px 15px;
  border-bottom: 3px solid #62ccf5;
  height: 30px;
  background-color: #fff;
} 

@media screen and (min-width: 992px){
#logo {
    width: 240px;
    height: 30px;
  }
}
#DBD { 
  position: absolute;
  right: 40px;
}
@media screen and (min-width: 992px) {
  #DBD {
    top: 15px;
    right: unset;
    left: 275px;
  }
}
#fb {
  position: absolute;
  right: 10px;

}
@media screen and (min-width: 992px) {
  #fb {
    top: 15px;
    right: unset;
    left: 370px;
  }
}

body {
  margin: 0;
  font-family: Anuphan, sans-serif;
  font-size: 16px;
  font-weight: normal;
  line-height: 1.625;
  color: #0b2760;
  background-color: #f5f5f7;
  text-rendering: geometricPrecision;
  min-height: 100vh;
  min-height: -webkit-fill-available;
  width: 100%;
  overflow-x: hidden;
}
.text-center {
  text-align: center;
}
.img-rounded {
  border-radius: 10px;
  vertical-align: middle;
  border-style: none;
  max-width: 100%;
  height: auto;
}

.text-blue {
  color: #0092d2;
}

.text-slogan {
  text-align: center;
  font-size: 16px;
  font-weight: normal;
  margin: -12px 0 16px;
}

.text-slogan2 {
  text-align: center;
  font-size: 16px;
  font-weight: normal;
  margin: -7px 0 9px;
}
.text-box {
  border: 1px solid #eee;
  padding: 15px;
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  font-size: 1.125em;
}

.text-title {
  font-size: 1.75em;
  font-weight: normal;
  color: #fff;
  margin-bottom: 10px;
  text-shadow: 0 1px 3px rgba(0, 0, 0, 0.12);
}

#content {
  margin: auto;
  padding-left: 15px;
  padding-right: 15px;
  padding-bottom: 100px;
}
/* @media screen and (min-width: 992px) {
  #content {
    padding: 80px 30px 20px;
  }
} */
@media screen and (min-width: 992px) {
  #content {
    padding: 20px 30px 40px;
  }
}

h1 {
  text-align: center;
  font-size: 1.125em;
  font-weight: bold;
  margin: 16px 0;
}
@media screen and (min-width: 992px) {
  h1 {
    font-size: 1.4em;
  }
}

.sec-title {
  font-size: 1.5em;
  text-align: center;
  overflow: hidden;
  margin-bottom: 8px;
}
.sec-title.-small {
  font-size: 1.125em;
}
.sec-title small {
  display: block;
  font-weight: 400;
  font-size: 16px;
  margin-top: -1px;
}
.sec-title .large {
  font-size: 1.6em;
}
.sec-title b {
  display: inline-block;
  border: 2px solid rgba(255, 255, 255, 0.5);
  border-radius: 5px;
  padding: 0 7px;
}
@media screen and (min-width: 480px) {
  .sec-title {
    font-size: 1.75em;
  }
}

.sub-title {
  font-size: 1.3em;
  text-align: center;
  font-weight: 500;
  transition: 0.3s ease;
  animation: blinking 1s infinite;
}
.sub-title.-not-blink {
  animation: none;
}
.sub-title small {
  display: block;
  font-weight: 400;
  font-size: 16px;
  margin-top: -1px;
}

.result-title {
  text-align: center;
  font-weight: normal;
  margin-bottom: 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  min-height: 64px;
}
.result-title span {
  padding-right: 6px;
}
.result-title small {
  width: 100%;
  font-weight: 400;
  font-size: 16px;
  margin-top: -1px;
  margin-bottom: var(--space);
}

.sec-box {
  border-radius: 7px;
  padding: 4px;
  background: #0092d2;
  max-width: 450px;
  margin: 0 auto var(--space);
  color: #fff;
}
.sec-box.-large {
  margin: auto;
  max-width: 690px;
  padding-left: 20px;
  padding-right: 20px;
}

.sec-box a {
  color: #fff;
}
.sec-box select {
  width: 100%;
}
.sec-box .input-grid,
.sec-box .input-group {
  margin-bottom: 16px;
}
.sec-box .text-info {
  max-width: 600px;
  margin: 0 auto;
}
.sec-box hr {
  margin-left: calc(var(--space) * -1);
  margin-right: calc(var(--space) * -1);
}

.table-contact {
  margin-left: auto ;
  margin-right: auto;
}
td {
  padding: 4px;
}

.showLotto {
  display: block;
  padding-top: 30px;
  margin-left: auto;
  margin-right: auto;
  max-width: 550px;
}

.input-page {
  padding: 20px; 
  text-align: center;
}

.input-form {
  padding: 10px; 
  text-align: center;
}

.input {
  padding: 12px 40px 12px 40px;
  border: 1px solid #22407c;
  font-size: 18px
}

.Submit {
  padding: 13px 12px 13px 12px;
  border: 1px solid #3c5fa5;
  background-color: #0092d2;
  cursor: pointer;
  color: aliceblue;
  font-size: 14px;
  
}
.select-date {
  font-size: 14px;
}
