.show {
  padding: 10px;
  font-size: 18px;
  letter-spacing: 0.1em;
}

.submited {
  padding: 10px;
  font-size: 22px;
}

.content-check {
  padding: 0px 15px;
}

.border-check {
  background: radial-gradient(100% 100% at 49.85% 0%, #d2edff 0%, #9fd9ff 100%);
  color: #0b2760;
  padding: 25px 20px 25px;
  margin-left: auto;
  margin-right: auto;
  max-width: 450px;
  border-radius: 8px;
}

.form-number {
  margin: 13px;
  color: #0b2760;
}

.sec-announce {
  background: radial-gradient(100% 100% at 49.85% 0%, #d2edff 0%, #9fd9ff 100%);
  color: #0b2760;
  padding: 27px 20px 20px;
  margin-left: auto;
  margin-right: auto;
  }
.sec-title2 {
    font-weight: 500;
    font-size: 22px;
  }

  .round {
    background: linear-gradient(90deg, #d4af37 0%, #fbe599 50%, #d5ab61 100%);
    font-size: 18px;
    padding: 5px 20px;
    border-radius: 5px;
    font-weight: 500;
    color: #473707;
    margin-bottom: 12px;
    display: inline-block;
  }
  .bg {
    background-color: #fff;
    border-radius: 0 0 5px 5px;
    border: 1px solid #3ba7ef;
    border-top: none;
  }
  .first {
    font-family: Arial, Helvetica, sans-serif;
    letter-spacing: 0.2em;
    font-size: 57px;
    line-height: 1.3;
    color: #d4af37;
    font-weight: bold;
    background: -webkit-linear-gradient(-90deg, #c79c52 10%, #e7c144 52%, #c79c52 52%, #d5ab61 80%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  h3 {
    font-size: 16px;
    padding: 2px 0 3px;
    margin: 10px 0 0;
    white-space: nowrap;
    font-weight: 500;
    background-color: #0092d2;
    background: linear-gradient(180deg, #5cbae2 0%, #3ba7ef 100%);
    border-radius: 5px 5px 0 0;
    color: #fff;
  }

.prize-1 h3{
    font-size: 18px;
  }

.prize-2-3 {
  display: flex;
  width: 100%;
  margin-top: 4px;
}
.prize-3 {
  width: 63%;
  padding-right: 12px;
}
.prize-3 .bg {
    display: flex;
    justify-content: space-evenly;
    font-size: 27px;
}
.prize-3 .bg   b {
      margin: 0 auto;
      color: #0b2760;
      background: none;
      background-clip: unset;
      -webkit-background-clip: unset;
      -webkit-text-fill-color: unset;
      letter-spacing: 0.2em;
    }
  
  .sep {
    width: 1px;
    background: #3ba7ef;
  }

.prize-2 {
  width: 37%;
  display: flex;
  flex-direction: column;
}
.prize-2  .bg {
    flex: 1;
    font-size: 67px;
}
.prize-2 .bg b {
      letter-spacing: 4px;
    }

.select-date {
  margin-left: 10px;
}